.hero-subtext,
.hero-tagline {
  align-self: stretch;
  position: relative;
  line-height: 67px;
}
.hero-subtext {
  font-size: 24px;
  line-height: 32px;
  font-family: Roboto;
}
.title5 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 5px;
}
.search-form-label {
  flex: 1;
  position: relative;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.radiobuttonunchecked-icon {
  position: relative;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  overflow: hidden;
}
.padding {
  border-radius: 19px;
  display: flex;
  flex-direction: row;
  padding: 9px;
  align-items: center;
  justify-content: flex-start;
}
.label1 {
  position: relative;
  letter-spacing: 0.15px;
  line-height: 150%;
}
.radio1 {
  overflow: hidden;
}
.flight-options,
.form-title-group,
.radio1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.flight-options {
  gap: 20px;
  font-size: 16px;
  color: rgba(48, 48, 48, 0.87);
}
.form-title-group {
  align-self: stretch;
  overflow: hidden;
  padding: 5px;
  gap: 5px;
}
.departure-autocomplete {
  align-self: stretch;
  flex: 1;
}
.inputs-row {
  flex: 1;
  display: flex;
  flex-direction: row;
  padding: 5px;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 10px;
}
.search-flights {
  position: relative;
  font-size: 15px;
  letter-spacing: 0.46px;
  line-height: 26px;
  text-transform: uppercase;
  font-weight: 500;
  font-family: Roboto;
  color: #fff;
  text-align: center;
}
.button-group,
.search-button1 {
  display: flex;
  justify-content: center;
}
.search-button1 {
  cursor: pointer;
  border: 0;
  padding: 0 10px;
  background-color: #f99a0e;
  border-radius: 4px;
  width: 164px;
  height: 56px;
  flex-shrink: 0;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
}
.search-button1:hover {
  background-color: #c87d0d;
}
.button-group {
  flex-direction: column;
  padding: 5px;
  align-items: flex-start;
}
.form-inputs-row,
.search-form {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.search-form {
  border-radius: 10px;
  background-color: #fff;
  flex-direction: column;
  padding: 20px;
  gap: 4px;
  text-align: left;
  font-size: 20px;
  color: #303030;
  font-family: Roboto;
}
.search-container,
.test-1 {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.search-container {
  align-self: stretch;
  height: 640px;
  flex-shrink: 0;
  padding: 0 140px;
  box-sizing: border-box;
  justify-content: center;
  gap: 43px;
  background-image: url(/public/search-container@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.test-1 {
  position: relative;
  justify-content: flex-start;
  text-align: center;
  font-size: 61px;
  color: #fff;
  font-family: "Baloo Bhai";
}
@media screen and (max-width: 960px) {
  .inputs-row {
    width: 100%;
    flex: unset;
    align-self: stretch;
  }
  .button-group {
    width: 100%;
    text-align: left;
  }
  .form-inputs-row,
  .search-form {
    flex-direction: column;
  }
  .search-container {
    padding-left: 30px;
    padding-right: 30px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .hero-tagline {
    font-size: 42px;
    line-height: 48px;
  }
  .search-form-label {
    width: 100%;
    padding-bottom: 10px;
    margin-bottom: 6px;
    flex: unset;
    align-self: stretch;
  }
  .flight-options {
    width: 100%;
  }
  .form-title-group {
    flex-direction: column;
    align-items: flex-start;
  }
  .departure-autocomplete {
    flex: unset;
    align-self: stretch;
  }
  .inputs-row {
    flex-direction: column;
  }
  .search-button1 {
    width: 100%;
  }
  .search-form {
    margin-top: 20px;
  }
  .search-container {
    height: 800px;
  }
}
